@font-face {
  font-family: "Aire Exterior";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/aire-exterior-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-light.woff2") format("woff2");
  font-display: swap;
}
html {
  height: 100%;
  box-sizing: border-box;
}

main {
  overflow: hidden;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  color: #ababab;
  font-weight: 300;
  font-size: 21.33px;
  line-height: 32px;
  background-color: #141c24;
  margin: 0;
  padding: 0;
}

body.lock {
  overflow: hidden;
}

img {
  display: block;
  height: 100%;
  width: 100%;
}

.input-hidden {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.section-wrapper {
  position: relative;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .section-wrapper {
    padding: 0 25px;
  }
}
@media (min-width: 1280px) {
  .section-wrapper::after {
    content: "";
    width: 1px;
    background-color: rgba(255, 255, 255, 0.15);
    display: block;
    position: absolute;
    top: 5%;
    bottom: 0;
    left: -30px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}

.link {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18.75px;
  font-weight: 500;
  color: #00c5ff;
  text-decoration: none;
  justify-self: center;
  padding: 5px 7px;
  border-bottom: 1px solid #00c5ff;
}
.link:hover {
  color: #57d6ff;
}
.link:active {
  color: #ababab;
}

.title-partition {
  color: #57d6ff;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  position: relative;
  margin: 0;
}
.title-partition::before {
  content: "";
  width: 50px;
  height: 1px;
  background-color: #57d6ff;
  display: inline-block;
  position: relative;
  right: 34px;
  top: -5px;
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1280px) {
  .navigation {
    flex-direction: row;
    flex-wrap: wrap;
    width: max-content;
    justify-content: space-between;
  }
}

.navigation__link {
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: #ffffff;
  padding: 10px 10px;
  overflow: hidden;
  display: block;
  text-decoration: none;
  position: relative;
}
.navigation__link:active {
  color: #00c5ff;
  transition: 0.3ms;
}
.navigation__link:focus-visible {
  outline: 2px solid #00c5ff;
  border-radius: 0.2em;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 14px 10px;
  }
}

.navigation__link::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background-color: #00c5ff;
  left: 0;
  bottom: 0;
  transition: all 0.3s ease;
}

.navigation__link:hover::after,
.navigation__link:hover {
  width: 100%;
  color: #ababab;
}

.social {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
  margin-bottom: 55px;
}

.social__item:not(:last-child) {
  margin-right: 15px;
}

.social__link {
  color: #ababab;
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__link:hover {
  color: #00c5ff;
}

.subtitle-partition {
  font-family: "Aire Exterior", "Arial", sans-serif;
  font-size: 39px;
  line-height: 39px;
  font-weight: 400;
  color: #ffffff;
}

.rate-card {
  display: flex;
  flex-direction: column;
  padding-top: 76px;
  padding-left: 15px;
  padding-bottom: 50px;
  padding-right: 7px;
  background-color: #1f2631;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}
.rate-card--beneficial {
  background-color: #00c5ff;
  color: #171717;
}
@media (min-width: 768px) {
  .rate-card {
    padding-left: 47px;
    padding-right: 38px;
    padding-bottom: 44px;
  }
}
@media (min-width: 1280px) {
  .rate-card:hover .rate-card__button-choose {
    background-color: #141c24;
    color: #00c5ff;
    transition: 0.5s;
  }
}

.rate-card--beneficial:hover .rate-card__button-choose--beneficial {
  color: #ffffff;
}

.rate-card__title {
  margin: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 42px;
  color: #ffffff;
  margin-bottom: 19px;
}
.rate-card__title--beneficial {
  color: #171717;
}

.rate-card__list,
.rate-card__list-inner {
  padding: 0;
  list-style: none;
}

.rate-card__list {
  margin-bottom: 66px;
  max-width: 282px;
}
@media (min-width: 768px) {
  .rate-card__list {
    flex-grow: 1;
  }
}

.rate-card__price {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 28px;
  display: block;
}

.rate-card__description {
  margin: 0;
}

.rate-card__button-choose {
  width: 100%;
  height: 62px;
  border: none;
  background-color: #00c5ff;
  font-family: "Aire Exterior", "Arial", sans-serif;
  color: #141c24;
  font-weight: 400;
  font-size: 24px;
  line-height: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 5px;
  margin-top: auto;
}
.rate-card__button-choose:active {
  transform: scale(1.1);
  transition: 0.5s;
}
.rate-card__button-choose--beneficial {
  background-color: #141c24;
  color: #00c5ff;
}
.rate-card__button-choose--beneficial:hover {
  color: #ffffff;
}
@media (min-width: 768px) {
  .rate-card__button-choose {
    height: 70px;
  }
}

@media (min-width: 1280px) {
  .rate__item:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  }
}

.input-form {
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #ababab;
  border-radius: 5px;
  border: none;
  font-family: "Aire Exterior", "Arial", sans-serif;
  width: 100%;
  padding-left: 20px;
  min-height: 75px;
}
@media (min-width: 1280px) {
  .input-form {
    font-size: 32px;
    line-height: 32px;
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
  position: fixed;
  z-index: 10001;
  transform: translateX(100vw);
  background-color: #141C24;
  transition: transform 0.2s ease;
}
.header--open {
  will-change: transform;
  transform: translateX(0);
  height: 100vh;
  overflow-y: scroll;
}
@media (min-width: 1280px) {
  .header {
    display: flex;
    background-color: #141c24;
    position: fixed;
    width: 100%;
    z-index: 555;
    padding: 0;
    align-items: start;
    box-shadow: 0 2px 10px #00c5ff;
    transform: translateX(0);
  }
}

.header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 41px;
  padding-left: 38px;
  padding-right: 38px;
  padding-bottom: 30px;
  width: 100vw;
  position: relative;
}
.header__container::after {
  width: 70%;
  height: 1px;
  content: "";
  background-color: rgb(255, 255, 255);
  bottom: 20px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 1280px) {
  .header__container {
    flex-direction: row;
    max-width: 1280px;
    align-items: center;
    padding-top: 32px;
    padding-bottom: 35px;
    margin: 0 auto;
    justify-content: flex-start;
  }
  .header__container::after {
    display: none;
  }
}

.header__navigation {
  width: 100%;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .header__navigation {
    margin: 0;
    width: auto;
  }
}

.header__logo-link {
  margin-bottom: 20px;
  transition: all 0.3s ease;
}
.header__logo-link:focus-visible {
  outline: 2px solid #00c5ff;
  border-radius: 0.2em;
}
@media (min-width: 768px) {
  .header__logo-link {
    color: #00c5ff;
  }
  .header__logo-link:hover {
    color: #ababab;
  }
}
@media (min-width: 1280px) {
  .header__logo-link {
    margin: 0;
    margin-right: 99px;
  }
}

@media (min-width: 1280px) {
  .header__logo-mobile {
    display: none;
  }
}

.header__logo-desktop {
  display: none;
}
@media (min-width: 1280px) {
  .header__logo-desktop {
    display: block;
  }
}

.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.header__item:not(:last-child) {
  margin-bottom: 11px;
}
@media (min-width: 1280px) {
  .header__item:not(:last-child) {
    margin-right: 26px;
    margin-bottom: 0;
  }
}

@media (min-width: 1280px) {
  .header__social-list {
    display: none;
  }
}
.header__social-list::after {
  content: "";
  width: 300px;
  height: 1px;
  background-color: #ffffff;
  display: block;
  position: absolute;
  bottom: -33px;
  left: 50%;
  transform: translateX(-50%);
}

.header__social-item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__back-menu {
  background-color: transparent;
  border: none;
  color: #ffffff;
}
.header__back-menu:hover {
  color: #00c5ff;
}
@media (min-width: 1280px) {
  .header__back-menu {
    display: none;
  }
}

@media (min-width: 1280px) {
  .intro {
    padding-top: 125px;
  }
}

.intro__card {
  background-image: url("../img/intro-background-desktop.png");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0 0 15px 15px;
  position: relative;
  padding-top: 30px;
  padding-bottom: 80px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .intro__card {
    padding: 260px 0 224px;
    max-width: 1440px;
    margin: 0 auto;
    outline: 1px solid #57d6ff;
    min-height: 777px;
  }
}
@media (min-width: 1280px) {
  .intro__card {
    background-image: none;
  }
  .intro__card::before {
    content: "";
    width: 1px;
    height: 77px;
    background-color: #fff;
    position: absolute;
    top: 150px;
  }
}

.intro__logo-link {
  margin-bottom: 125px;
}
@media (min-width: 1280px) {
  .intro__logo-link {
    display: none;
  }
}

@media (min-width: 768px) {
  .intro__logo {
    width: 400px;
    height: 70px;
  }
}

.intro__text {
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: #ffffff;
  max-width: 142px;
  margin: 0;
  margin-bottom: 11px;
  align-self: start;
}
@media (min-width: 768px) {
  .intro__text {
    align-self: center;
    max-width: 220px;
    margin-bottom: 10px;
  }
}

.intro__slogan {
  font-size: 48px;
  line-height: 48px;
  font-family: "Aire Exterior", "Arial", sans-serif;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-bottom: 137px;
}
@media (min-width: 768px) {
  .intro__slogan {
    margin-bottom: 96px;
  }
}
@media (min-width: 1280px) {
  .intro__slogan {
    margin-bottom: 150px;
  }
}

.intro__upgrade {
  background-color: #ffffff;
  color: #171717;
  font-family: "Aire Exterior", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  border: none;
  width: 230px;
  height: 61px;
  text-transform: uppercase;
  padding: 0;
  position: relative;
  padding-left: 21px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.intro__upgrade:hover {
  color: #00c5ff;
}
.intro__upgrade::after {
  content: "";
  background-color: transparent;
  width: 21px;
  height: 61px;
  display: block;
  position: absolute;
  top: 0;
  left: 100%;
  border-left: 21px solid #ffffff;
  border-top: 24px solid transparent;
}
@media (min-width: 768px) {
  .intro__upgrade {
    margin-right: 22px;
  }
}

.intro__menu-toggle {
  width: 52px;
  height: 20px;
  display: flex;
  align-items: start;
  background-color: transparent;
  border: none;
  position: absolute;
  padding: 0;
  bottom: 34px;
  right: 15px;
}
@media (min-width: 1280px) {
  .intro__menu-toggle {
    display: none;
  }
}

.intro__menu-toggle span {
  width: 100%;
  height: 5px;
  background-color: #eae5e5;
  display: block;
  box-shadow: 0 15px 0 #eae5e5;
}

.intro__slider {
  display: none;
}
@media (min-width: 1280px) {
  .intro__slider {
    width: 100%;
    max-width: 1440px;
    height: 777px;
    overflow: hidden;
    position: absolute;
    z-index: -2;
    top: 0;
    display: block;
  }
}

.intro__slider-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.intro__slide {
  flex-shrink: 0;
}

.intro__slide img {
  border-radius: 0 0 15px 15px;
}

.intro__slider__scrollbar {
  position: absolute;
  width: 2px;
  height: 520px;
  background-color: #ffffff;
  top: 140px;
  right: 50px;
}

.intro__slider__scrollbar .swiper-scrollbar-drag {
  background-color: #57d6ff;
  width: 2px;
}

@media (min-width: 1280px) {
  .intro__link-line {
    width: 1px;
    height: 130px;
    background-color: #ffffff;
    position: absolute;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    transition: height 0.3s ease;
  }
}

.overlay {
  transition: all 0.3s ease;
  display: none;
}
@media (min-width: 1280px) {
  .overlay {
    position: absolute;
    inset: 0;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0.2);
    opacity: 0;
    display: block;
  }
}

.manual {
  padding-top: 75px;
  padding-bottom: 110px;
  position: relative;
}
@media (min-width: 1280px) {
  .manual {
    padding-bottom: 153px;
  }
}

.manual__card-description {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, min-content);
  justify-items: center;
  padding-bottom: 118px;
}
@media (min-width: 768px) {
  .manual__card-description {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(5, min-content);
    position: relative;
    padding-bottom: 87px;
  }
}

.manual__title-card-description {
  margin-right: -33px;
  margin-bottom: 17px;
  justify-self: center;
}
@media (min-width: 768px) {
  .manual__title-card-description {
    grid-column: 2/-1;
    grid-row: 1/2;
    justify-self: start;
    padding-left: 42px;
    margin-bottom: 23px;
  }
}

.manual__subtitle-card-description {
  justify-self: start;
  margin-bottom: 61px;
}
@media (min-width: 768px) {
  .manual__subtitle-card-description {
    grid-column: 2/-1;
    grid-row: 2/3;
  }
}

.manual__description-card {
  margin: 0;
  max-width: 310px;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .manual__description-card {
    grid-column: 2/-1;
    max-width: 471px;
    margin-bottom: 47px;
    grid-row: 3/4;
  }
}

.manual__img-box {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .manual__img-box {
    grid-column: 1/2;
    grid-row: 2/7;
  }
}

@media (min-width: 768px) {
  .manual__img-card-description {
    width: 499px;
  }
}
@media (min-width: 1280px) {
  .manual__img-card-description {
    width: 664px;
  }
}

.manual__description-characteristic {
  margin: 0;
  max-width: 319px;
  margin-bottom: 81px;
}
@media (min-width: 768px) {
  .manual__description-characteristic {
    grid-column: 2/-1;
    max-width: 471px;
    grid-row: 4/5;
    margin-bottom: 71px;
  }
}

.manual__download-module {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18.75px;
  font-weight: 500;
  color: #00c5ff;
  text-decoration: none;
  justify-self: center;
  padding: 5px 3px;
  border-bottom: 1px solid #00c5ff;
}
@media (min-width: 768px) {
  .manual__download-module {
    grid-column: 2/-1;
    grid-template-rows: 5/6;
    justify-self: start;
  }
}

.manual__card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.manual__title {
  margin-right: -33px;
  margin-bottom: 16px;
}

.manual__subtitle {
  grid-column: 1/-1;
  grid-row: 2/3;
  margin-bottom: 90px;
  align-self: start;
}
@media (min-width: 768px) {
  .manual__subtitle {
    align-self: center;
  }
}

.manual__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: start;
  column-gap: 65px;
  row-gap: 53px;
  list-style: none;
  font-size: 21px;
  line-height: 32px;
  padding: 0;
  margin: 0;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .manual__list {
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    column-gap: 61px;
    margin-bottom: 84px;
  }
}

.manual__item {
  padding-top: 58px;
  text-align: center;
  max-width: 130px;
}
@media (min-width: 768px) {
  .manual__item {
    max-width: 183px;
    padding-top: 75px;
  }
}
.manual__item--account {
  background-image: url("../img/icons/mail-mob.png");
  background-size: 51px 50px;
  background-position: top center;
  background-repeat: no-repeat;
  position: relative;
}
.manual__item--account::after {
  content: "";
  width: 58px;
  height: 1px;
  display: inline-block;
  background-color: #57d6ff;
  position: absolute;
  right: -58px;
  top: 24px;
}
@media (min-width: 768px) {
  .manual__item--account {
    background-image: url("../img/icons/mail-desk.png");
    background-size: 64px 64px;
  }
  .manual__item--account::after {
    width: 73px;
  }
}
.manual__item--download {
  background-image: url("../img/icons/download-mob.png");
  background-size: 51px 50px;
  background-position: top center;
  background-repeat: no-repeat;
  position: relative;
}
.manual__item--download::before {
  content: "";
  width: 162px;
  height: 1px;
  background-color: #57d6ff;
  position: absolute;
  transform: rotate(135deg);
  left: -120px;
  bottom: 16px;
}
@media (min-width: 768px) {
  .manual__item--download {
    background-image: url("../img/icons/download-desk.png");
    background-size: 64px 64px;
  }
  .manual__item--download::before {
    display: none;
  }
  .manual__item--download::after {
    content: "";
    width: 73px;
    height: 1px;
    display: inline-block;
    background-color: #57d6ff;
    position: absolute;
    right: -58px;
    top: 24px;
  }
}
.manual__item--rate {
  background-image: url("../img/icons/joystick-mob.png");
  background-size: 56px 56px;
  background-position: top center;
  background-repeat: no-repeat;
