.intro {
  @include viewport--lg {
    padding-top: 125px;
  }
}

.intro__card {
  background-image: url("../img/intro-background-desktop.png");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0 0 15px 15px;
  position: relative;
  padding-top: 30px;
  padding-bottom: 80px;
  padding-left: 20px;
  padding-right: 20px;

  @include viewport--md {
    padding: 260px 0 224px;
    max-width: 1440px;
    margin: 0 auto;
    outline: 1px solid $color-light-blue;
    min-height: 777px;
  }

  @include viewport--lg {
    background-image: none;
    &::before {
      content: "";
      width: 1px;
      height: 77px;
      background-color: #fff;
      position: absolute;
      top: 150px;
    }
  }
}

.intro__logo-link {
  margin-bottom: 125px;

  @include viewport--lg {
    display: none;
  }
}

.intro__logo {
  @include viewport--md {
    width: 400px;
    height: 70px;
  }
}

.intro__text {
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: $color-white;
  max-width: 142px;
  margin: 0;
  margin-bottom: 11px;
  align-self: start;

  @include viewport--md {
    align-self: center;
    max-width: 220px;
    margin-bottom: 10px;
  }
}

.intro__slogan {
  font-size: 48px;
  line-height: 48px;
  font-family: $font-aire;
  font-weight: 400;
  color: $color-white;
  margin: 0;
  margin-bottom: 137px;

  @include viewport--md {
    margin-bottom: 96px;
  }

  @include viewport--lg {
    margin-bottom: 150px;
  }
}

.intro__upgrade {
  background-color: $color-white;
  color: $color-black;
  font-family: $font-aire;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  border: none;
  width: 230px;
  height: 61px;
  text-transform: uppercase;
  padding: 0;
  position: relative;
  padding-left: 21px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  @include trans;

  &:hover {
    color: $color-blue;
  }

  &::after {
    content: "";
    background-color: transparent;
    width: 21px;
    height: 61px;
    display: block;
    position: absolute;
    top: 0;
    left: 100%;
    border-left: 21px solid #ffffff;
    border-top: 24px solid transparent;
  }

  @include viewport--md {
    margin-right: 22px;
  }
}

.intro__menu-toggle {
  width: 52px;
  height: 20px;
  display: flex;
  align-items: start;
  background-color: transparent;
  border: none;
  position: absolute;
  padding: 0;
  bottom: 34px;
  right: 15px;


  @include viewport--lg {
    display: none;
  }
}

.intro__menu-toggle span {
  width: 100%;
  height: 5px;
  background-color: #eae5e5;
  display: block;
  box-shadow: 0 15px 0 #eae5e5;
}

.intro__slider {
  display: none;
  @include viewport--lg {
    width: 100%;
    max-width: 1440px;
    height: 777px;
    overflow: hidden;
    position: absolute;
    z-index: -2;
    top: 0;
    display: block;
  }
}

.intro__slider-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.intro__slide {
  flex-shrink: 0;
}

.intro__slide img {
  border-radius: 0 0 15px 15px;
}

.intro__slider__scrollbar {
  position: absolute;
  width: 2px;
  height: 520px;
  background-color: #ffffff;
  top: 140px;
  right: 50px;
}

.intro__slider__scrollbar .swiper-scrollbar-drag {
  background-color: $color-light-blue;
  width: 2px;
}

.intro__link-line {

  @include viewport--lg {
    width: 1px;
    height: 130px;
    background-color: $color-white;
    position: absolute;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    transition: height 0.3s ease;
  }

}

.overlay {
  @include trans;
  display: none;
    @include viewport--lg {
    position: absolute;
    inset: 0;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0.2);
    opacity: 0;
    display: block;
  }
}
