@mixin list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

@mixin trans {
  transition: all 0.3s ease;
}

@mixin link-reset {
  display: block;
  text-decoration: none;
}

@mixin viewport--md {
  @media (min-width: $md) {
    @content;
  }
}

@mixin viewport--lg {
  @media (min-width: $lg) {
    @content;
  }
}
